.header {
  position: absolute;
  z-index: 5;
  top: 30px;
  left: 0;
  width: 100%;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
    background: #fff;
    border-radius: 20px;
    padding: 24px;
  }
  &__right {
    display: flex;
    align-items: center;
    gap: 40px;
  }
  &__logo {
    width: 266px;
    display: block;
    img {
      width: 100%;
    }
  }
  .menu {
    display: flex;
    align-items: center;
    gap: 40px;
    &-item {
      font-size: 16px;
      line-height: 120%;
      font-weight: 500;
      letter-spacing: -0.32px;
      color: #999999;
      &:hover {
        color: #0C0C0C;
      }
    }
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 16px;
  }
  &__btn {
    height: 58px;
    border-radius: 15px;
    padding: 5px 9px 5px 29px;
    span {
      font-size: 16px;
      letter-spacing: -0.32px;
    }
    i {
      width: 40px;
      height: 40px;
    }
  }
}
.burger {
  position: relative;
  z-index: 12;
  width: 32px;
  height: 20px;
  display: none;
  cursor: pointer;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 3px;
    background: linear-gradient(166.26deg, rgb(102, 175, 255) -5.343%,rgb(0, 122, 255) 104.022%);
    border-radius: 3px;
    transition: all 0.5s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
  &--active {
    span:nth-child(1) {
      top: 9px;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 8px;
      transform: rotate(45deg);
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    top: 16px;
    &__inner {
      gap: 16px;
      padding: 16px;
    }
    .menu {
      position: fixed;
      z-index: 10;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      flex-direction: column;
      background: #F5F5F6;
      padding: 120px 15px 50px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      overflow-y: auto;
      &--active {
        opacity: 1;
        pointer-events: auto;
      }
    }
  }
  .burger {
    display: block;
  }
}

@media screen and (max-width: 576px) {
  .header {
    &__inner {
      gap: 12px;
      border-radius: 16px;
      padding: 14px 12px;
    }
    &__logo {
      width: 170px;
    }
    &__group {
      gap: 12px;
    }
    &__btn {
      height: 36px;
      border-radius: 8px;
      padding: 3px 12px;
      span {
        font-size: 14px;
      }
      i {
        display: none !important;
      }
    }
  }
  .burger {
    width: 25px;
    height: 18px;
    &--active {
      span:nth-child(1) {
        top: 7px;
      }
    }
  }
}

@media screen and (max-width: 359px) {
  .header {
    &__logo {
      width: 150px;
    }
    &__btn {
      height: 32px;
      padding: 3px 10px;
      span {
        font-size: 12px;
      }
    }
  }
}