.footer {
  position: relative;
  padding: 15px 0 30px;
  &:after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 205px;
    background: #ECECEE;
    pointer-events: none;
  }
  &__inner {
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: start;
    gap: 30px;
  }
  &__logo {
    width: 155px;
    flex-shrink: 0;
    display: block;
    img {
      width: 100%;
    }
  }
  .menu {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 20px 40px;
    &-item {
      font-size: 16px;
      line-height: 120%;
      font-weight: 500;
      letter-spacing: -0.32px;
      color: #999999;
      &:hover {
        color: #0C0C0C;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .footer {
    &:after {
      display: none;
    }
    &__inner {
      flex-direction: column;
      align-items: center;
      gap: 20px;
    }
    .menu {
      flex-direction: column;
      gap: 20px;
    }
  }
}