.contacts {
  padding: 200px 0 50px;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
  }
  &__title {
    flex-shrink: 0;
    font-size: 48px;
    line-height: 120%;
    font-weight: 600;
    letter-spacing: -0.96px;
  }
  &__text {
    font-size: 16px;
    line-height: 212%;
  }
  &__items {
    display: grid;
    grid-template-columns: 0.73fr 1.27fr;
    gap: 20px;
    margin-top: 20px;
  }
  &__item {
    background: #fff;
    border-radius: 30px;
    padding: 30px;
    @media screen and (min-width: 993px) {
      &--big {
        grid-column: 2/3;
        grid-row: 1/3;
        .contacts__subitem {
          justify-content: space-between;
        }
      }
      &--big2 {
        grid-column: 1/3;
      }
    }
    &-group {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 20px;
    }
  }
  &__item-title {
    display: flex;
    align-items: center;
    gap: 10px;
    i {
      max-width: 128px;
      img {
        max-width: 100%;
      }
    }
    span {
      font-size: 18px;
      line-height: 120%;
      font-weight: 600;
    }
  }
  &__subitems {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 20px;
  }
  &__subitem {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 10px;
    font-size: 16px;
    line-height: 120%;
    font-weight: 500;
    letter-spacing: -0.32px;
    color: #999999;
    a {
      color: #169FE7;
    }
  }
  &__map {
    position: relative;
    padding-bottom: 52%;
    margin-top: 30px;
    iframe {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 30px;
    }
  }
  &__socials {
    display: flex;
    align-items: center;
    gap: 11.5px;
  }
  &__social {
    position: relative;
    width: 45px;
    height: 45px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 50%;
    }
    img {
      max-width: 80%;
      max-height: 80%;
    }
  }
  .twitter {
    background: #55ACE3;
  }
  .facebook {
    background: #4E6297;
  }
  .instagram {
    background: linear-gradient(44.16deg, rgb(238, 160, 84) 16.157%,rgb(214, 71, 99) 50.105%,rgb(182, 42, 153) 83.68%);
  }
}

@media screen and (max-width: 992px) {
  .contacts {
    padding: 180px 0 30px;
    &__title {
      font-size: 32px;
    }
    &__items {
      grid-template-columns: 100%;
    }
    &__item {
      &--big {
        order: 2;
      }
      &--big2 {
        order: 3;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .contacts {
    padding-top: 160px;
    &__top {
      flex-direction: column;
      align-items: start;
    }
  }
}

@media screen and (max-width: 576px) {
  .contacts {
    padding: 120px 0 20px;
    &__item {
      padding: 25px 20px;
      &-group {
        flex-direction: column;
        text-align: center;
        gap: 10px;
      }
    }
    &__map {
      min-height: 260px;
    }
  }
}