.main {
  position: relative;
  background: #0C0C0C;
  border-radius: 0 0 200px 200px;
  padding: 200px 0 115px;
  overflow: hidden;
  &__inner {
    position: relative;
    z-index: 2;
  }
  &__title {
    position: relative;
    z-index: 1;
    max-width: 885px;
    font-size: 56px;
    line-height: 150%;
    font-weight: 600;
    color: #fff;
    span {
      color: #3E9AFF;
    }
  }
  &__text {
    position: relative;
    z-index: 1;
    max-width: 416px;
    font-size: 18px;
    line-height: 120%;
    font-weight: 500;
    letter-spacing: -0.36px;
    color: #ACB7C2;
    margin-top: 24px;
  }
  &__items {
    position: relative;
    z-index: 1;
    max-width: 540px;
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 136px / 3));
    gap: 68px;
    margin-top: 48px;
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 15px;
    &-icon {
      width: 40px;
      height: 40px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #fff;
      border-radius: 12px;
      padding: 5px;
      img {
        max-width: 100%;
      }
    }
    &-text {
      font-size: 16px;
      line-height: 120%;
      font-weight: 500;
      letter-spacing: -0.32px;
      color: #fff;
    }
  }
  &__btn {
    position: relative;
    z-index: 1;
    height: 68px;
    display: inline-flex;
    border-radius: 18px;
    padding: 5px 10px 5px 32px;
    margin-top: 56px;
    span {
      font-size: 18px;
      letter-spacing: -0.36px;
    }
    i {
      width: 47px;
      height: 47px;
    }
  }
  &__img {
    position: absolute;
    right: -60px;
    bottom: -115px;
    max-width: 744px;
    pointer-events: none;
    img {
      max-width: 100%;
      display: block;
    }
  }
  .circle {
    position: absolute;
    top: 140px;
    left: 40px;
    width: 305px;
    height: 305px;
    background: #449EFF;
    filter: blur(200px);
    border-radius: 50%;
    pointer-events: none;
  }
  .circle2 {
    position: absolute;
    top: 196px;
    right: 74px;
    width: 305px;
    height: 305px;
    background: #449EFF;
    filter: blur(200px);
    border-radius: 50%;
    pointer-events: none;
  }
  .circle3 {
    position: absolute;
    left: 50%;
    bottom: -70px;
    transform: translate(-50%, 0);
    width: 305px;
    height: 305px;
    background: #449EFF;
    filter: blur(200px);
    border-radius: 50%;
    pointer-events: none;
  }
}
.partners {
  padding: 100px 0 50px;
  &__items {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 55px;
  }
  &__item {
    max-width: 245px;
    img {
      max-width: 100%;
    }
  }
}
.advantages {
  padding: 50px 0;
  &__title {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    text-align: center;
    .title-logo {
      width: 155px;
      display: flex;
      align-items: center;
      margin: 0 20px;
      img {
        width: 100%;
      }
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 240px / 3));
    gap: 120px;
    margin-top: 50px;
  }
}
.advantage-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &__img {
    max-width: 168px;
    height: 168px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    border-radius: 30px;
    padding: 15px;
    img {
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__desc {
    margin-top: 30px;
  }
  &__title {
    font-size: 20px;
    line-height: 180%;
    font-weight: 600;
  }
  &__text {
    font-size: 16px;
    line-height: 180%;
    letter-spacing: -0.32px;
    color: #999999;
    margin-top: 10px;
  }
}
.analytics {
  padding: 50px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 60px;
    padding-right: max(30px, calc(100% - 1200px) / 2);
  }
  &__img {
    width: calc(100% - 560px);
    img {
      width: 100%;
      height: 100%;
      border-radius: 0 62px 62px 0;
    }
  }
  &__desc {
    width: 500px;
  }
  &__subtitle {
    margin-top: 12px;
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 28px;
    li {
      position: relative;
      padding-left: 48px;
      font-size: 16px;
      line-height: 180%;
      letter-spacing: -0.32px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        width: 32px;
        height: 32px;
        background-image: url("../img/analytics-icon.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
      }
      span {
        font-weight: 600;
        color: #1485FF;
      }
    }
  }
}
.info {
  padding: 50px 0;
  &__title {
    text-align: center;
  }
  &__subtitle {
    text-align: center;
    margin-top: 10px;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 24px / 2));
    gap: 24px;
    margin-top: 30px;
  }
}
.info-card {
  display: flex;
  align-items: center;
  gap: 40px;
  background: #fff;
  border-radius: 32px;
  padding: 36px 30px;
  &__img {
    width: 140px;
    flex-shrink: 0;
    img {
      width: 100%;
    }
  }
  &__title {
    font-size: 20px;
    line-height: 180%;
    font-weight: 600;
  }
  &__text {
    font-size: 16px;
    line-height: 180%;
    letter-spacing: -0.32px;
    color: #999999;
    margin-top: 10px;
  }
}
.feedback {
  padding: 50px 0;
  &__title {
    text-align: center;
  }
  &__subtitle {
    text-align: center;
    margin-top: 10px;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
    gap: 20px;
    margin-top: 30px;
  }
  &__bottom {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
  }
  &__btn {
    height: 68px;
    border-radius: 18px;
    padding: 5px 10px 5px 32px;
    span {
      font-size: 18px;
      letter-spacing: -0.36px;
    }
    i {
      width: 47px;
      height: 47px;
    }
  }
}
.feedback-card {
  background: #fff;
  border-radius: 32px;
  padding: 28px;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
  }
  &__user {
    display: flex;
    align-items: center;
    gap: 16px;
    &-img {
      width: 49px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    &-name {
      font-size: 16px;
      line-height: 180%;
      font-weight: 600;
    }
    &-location {
      font-size: 14px;
      line-height: 180%;
      color: #999999;
    }
  }
  &__star {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    gap: 10px;
    span {
      font-size: 16px;
      line-height: 180%;
    }
    i {
      width: 13px;
      flex-shrink: 0;
      svg {
        width: 100%;
        height: auto;
      }
    }
  }
  &__text {
    margin-top: 5px;
    p {
      font-size: 16px;
      line-height: 180%;
    }
  }
}
.connection {
  position: relative;
  z-index: 2;
  padding: 50px 0 15px;
  &__inner {
    position: relative;
    min-height: 372px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #0C0C0C;
    border-radius: 32px;
    padding: 32px 30px 45px;
    overflow: hidden;
  }
  &__wrap--active {
    display: none;
  }
  &__title {
    position: relative;
    z-index: 2;
    text-align: center;
    color: #fff;
  }
  &__subtitle {
    position: relative;
    z-index: 2;
    text-align: center;
    margin-top: 5px;
  }
  .form {
    position: relative;
    z-index: 2;
    max-width: 732px;
    display: grid;
    grid-template-columns: 1.2fr 1.2fr 0.6fr;
    gap: 20px;
    margin: 16px auto 0;
    &-group {
      input {
        width: 100%;
        height: 68px;
        background: #fff;
        border: 1px solid transparent;
        border-radius: 18px;
        padding: 0 30px;
        font-size: 16px;
        line-height: 120%;
        color: #0C0C0C;
        &::placeholder {
          color: #ACB7C2;
        }
      }
      textarea {
        width: 100%;
        height: 95px;
        background: #fff;
        border: 1px solid transparent;
        border-radius: 18px;
        padding: 20px 30px;
        font-size: 16px;
        line-height: 120%;
        color: #0C0C0C;
        resize: none;
        &::placeholder {
          color: #ACB7C2;
        }
      }
    }
    @media screen and (min-width: 769px) {
      &-group--big {
        grid-column: 1/4;
      }
    }
    &-btn {
      height: 68px;
      border-radius: 18px;
      padding: 5px 15px;
      span {
        font-size: 18px;
        letter-spacing: -0.36px;
      }
    }
  }
  .circle {
    position: absolute;
    left: 30px;
    bottom: -125px;
    width: 305px;
    height: 305px;
    background: #449EFF;
    filter: blur(200px);
    border-radius: 50%;
    pointer-events: none;
  }
  .circle2 {
    position: absolute;
    top: -125px;
    right: 30px;
    width: 305px;
    height: 305px;
    background: #449EFF;
    filter: blur(200px);
    border-radius: 50%;
    pointer-events: none;
  }
}
.policy {
  padding: 200px 0 50px;
  &__inner {
    h2 {
      font-size: 36px;
      line-height: 160%;
      font-weight: 700;
      margin-bottom: 12px;
    }
    p {
      font-size: 18px;
      line-height: 188%;
    }
    hr {
      width: 100%;
      height: 30px;
      padding: 0;
      margin: 0;
      opacity: 0;
      pointer-events: none;
    }
    a {
      display: inline-flex;
      align-items: center;
      gap: 10px;
      color: #4C6FED;
      margin-left: 5px;
      i {
        width: 20px;
        flex-shrink: 0;
        img {
          width: 100%;
        }
      }
      span {
        font-size: 24px;
        line-height: 140%;
        font-weight: 500;
      }
    }
  }
}

@media screen and (max-width: 1400px) {
  .main {
    border-radius: 0 0 100px 100px;
    &__img {
      width: 54vw;
    }
  }
}

@media screen and (max-width: 1200px) {
  .main {
    &__title {
      max-width: 800px;
      font-size: 48px;
    }
    &__items {
      max-width: 500px;
      grid-template-columns: repeat(3, calc(100% / 3 - 80px / 3));
      gap: 40px;
    }
  }
  .analytics {
    &__inner {
      gap: 30px;
    }
    &__img {
      width: 50%;
    }
    &__desc {
      width: 50%;
    }
  }
}

@media screen and (max-width: 992px) {
  .main {
    padding-top: 180px;
    &__title {
      max-width: 500px;
      font-size: 32px;
    }
    &__text {
      margin-top: 16px;
    }
    &__items {
      max-width: 450px;
      grid-template-columns: repeat(3, calc(100% / 3 - 48px / 3));
      gap: 24px;
      margin-top: 32px;
    }
    &__btn {
      height: 58px;
      margin-top: 48px;
      span {
        font-size: 16px;
        letter-spacing: -0.32px;
      }
      i {
        width: 40px;
        height: 40px;
      }
    }
  }
  .partners {
    padding: 60px 0 30px;
    &__items {
      gap: 32px;
    }
  }
  .advantages {
    padding: 30px 0;
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 60px / 3));
      gap: 30px;
      margin-top: 30px;
    }
  }
  .analytics {
    padding: 30px 0;
    &__inner {
      flex-direction: column;
      padding: 0 15px;
    }
    &__img {
      width: 100%;
      img {
        border-radius: 30px;
      }
    }
    &__desc {
      width: 100%;
      order: -1;
    }
  }
  .info {
    padding: 30px 0;
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
      gap: 20px;
    }
  }
  .info-card {
    gap: 20px;
    &__img {
      width: 80px;
    }
  }
  .feedback {
    padding: 30px 0;
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
    }
    &__btn {
      height: 58px;
      span {
        font-size: 16px;
        letter-spacing: -0.32px;
      }
      i {
        width: 40px;
        height: 40px;
      }
    }
  }
  .connection {
    padding: 30px 0 15px;
  }
  .policy {
    padding: 160px 0 30px;
    &__inner {
      p {
        font-size: 16px;
      }
      hr {
        height: 20px;
      }
      a {
        span {
          font-size: 18px;
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .main {
    border-radius: 0 0 50px 50px;
    padding: 140px 0 60px;
    &__img {
      display: none;
    }
    .circle,
    .circle2,
    .circle3 {
      width: 250px;
      height: 250px;
    }
  }
  .partners {
    &__items {
     display: grid;
      grid-template-columns: repeat(3, calc(100% / 3 - 64px / 3));
    }
  }
  .advantages {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 30px / 2));
    }
  }
  .info {
    &__cards {
      grid-template-columns: 100%;
    }
  }
  .feedback {
    &__cards {
      grid-template-columns: 100%;
    }
  }
  .connection {
    .form {
      grid-template-columns: 100%;
      gap: 16px;
      &-btn {
        order: 2;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    border-radius: 0 0 30px 30px;
    padding-top: 120px;
    padding-bottom: 40px;
    &__items {
      grid-template-columns: 100%;
      gap: 16px;
    }
    &__btn {
      width: 100%;
    }
    .circle3 {
      display: none;
    }
  }
  .partners {
    padding: 40px 0 20px;
  }
  .advantages {
    padding: 20px 0;
    &__cards {
      grid-template-columns: 100%;
    }
  }
  .advantage-card {
    &__desc {
      max-width: 320px;
      margin-top: 15px;
    }
    &__title {
      font-size: 18px;
    }
  }
  .analytics {
    padding: 20px 0;
    &__inner {
      padding: 0 10px;
    }
  }
  .info-card {
    padding: 24px 20px;
  }
  .feedback {
    padding: 20px 0;
    &__btn {
      width: 100%;
    }
  }
  .feedback-card {
    padding: 24px 20px;
  }
  .connection {
    &__inner {
      padding: 30px 20px;
    }
  }
  .policy {
    padding: 120px 0 20px;
    &__inner {
      h2 {
        font-size: 26px;
      }
    }
  }
}

@media screen and (max-width: 359px) {
  .partners {
    &__items {
      grid-template-columns: repeat(2, calc(50% - 32px / 2));
    }
  }
}
