.wrapper {
  overflow: hidden;
}
.btn-blue {
  gap: 18px;
  background: linear-gradient(166.26deg, rgb(102, 175, 255) -5.343%,rgb(0, 122, 255) 104.022%);
  animation-name: btn;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  &:hover {
    background: linear-gradient(166.26deg, rgb(0, 122, 255) 104.022%, rgb(102, 175, 255) -5.343%);
  }
  span {
    font-family: 'Inter';
    line-height: 120%;
    font-weight: 600;
    color: #fff;
  }
  i {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    border-radius: 12px;
    padding: 5px;
    img {
      max-width: 100%;
    }
  }
}
@keyframes btn {
  0% {
    box-shadow: 0 0 5px rgb(102, 175, 255), 0 0 10px #017dff, 0 0 15px rgb(0, 122, 255);
  }
  50% {
    box-shadow: 0 0 5px #017dff, 0 0 15px #017dff, 0 0 25px #017dff;
  }
  100% {
    box-shadow: 0 0 5px rgb(102, 175, 255), 0 0 10px #017dff, 0 0 15px rgb(0, 122, 255);
  }
}
.h2 {
  font-size: 36px;
  line-height: 160%;
  font-weight: 700;
}
.h3 {
  font-size: 24px;
  line-height: 120%;
  font-weight: 600;
  letter-spacing: -0.48px;
}
.subtitle {
  font-size: 16px;
  line-height: 180%;
  letter-spacing: -0.32px;
  color: #999999;
}
.form-modal {
  position: relative;
  z-index: 2;
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  &--active {
    display: flex;
  }
  &__icon {
    width: 89px;
    img {
      width: 100%;
    }
  }
  &__title {
    color: #fff;
    margin-top: 12px;
  }
}
.left-anim {
  transform: translate(-40%, 0);
  opacity: 0;
  transition: all 0.6s;
}
.left-anim.active {
  transform: translate(0,0);
  opacity: 1;
}
.right-anim {
  transform: translate(40%, 0);
  opacity: 0;
  transition: all 0.6s;
}
.right-anim.active {
  transform: translate(0,0);
  opacity: 1;
}

@media screen and (max-width: 576px) {
  .h2 {
    font-size: 26px;
  }
  .h3 {
    font-size: 20px;
    letter-spacing: -0.4px;
  }
}