.blog-main {
  padding: 200px 0 25px;
  &__title {
    font-size: 40px;
    line-height: 120%;
    font-weight: 600;
    letter-spacing: -0.8px;
  }
  &__text {
    max-width: 1055px;
    margin-top: 15px;
    p {
      font-size: 16px;
      line-height: 130%;
      color: #3D404A;
    }
  }
  &__menu {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 12px;
    margin-top: 20px;
    li {
      display: flex;
      justify-content: center;
      align-items: center;
      background: #fff;
      border-radius: 16px;
      padding: 5.5px 16px;
      font-size: 14px;
      line-height: 150%;
      font-weight: 500;
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 20px;
  }
}
.blog-main__card {
  display: flex;
  justify-content: space-between;
  gap: 12px;
  &-desc {
    max-width: 650px;
  }
  &-subtitle {
    font-size: 14px;
    line-height: 150%;
    color: #3D404A;
  }
  &-title {
    display: block;
    font-size: 18px;
    line-height: 110%;
    font-weight: 700;
    margin-top: 10px;
    &:hover {
      color: #66AFFF;
    }
  }
  &-text {
    margin-top: 10px;
    p {
      font-size: 16px;
      line-height: 130%;
      color: #3D404A;
    }
  }
  &-link {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    border-radius: 16px;
    padding: 5.5px 20px;
    font-size: 14px;
    line-height: 150%;
    font-weight: 500;
    margin-top: 10px;
    &:hover {
      background: #66AFFF;
      color: #fff;
    }
  }
  &-img {
    width: 270px;
    flex-shrink: 0;
    img {
      width: 100%;
      border-radius: 20px;
    }
  }
}
.blog-featured {
  padding: 25px 0;
  &__cards {
    display: grid;
    grid-template-columns: repeat(6, calc(100% / 6 - 150px / 6));
    gap: 30px;
    margin-top: 25px;
  }
}
.blog-featured__card {
  &-img {
    position: relative;
    display: block;
    padding-bottom: 57%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 20px;
    }
  }
  &-desc {
    margin-top: 12px;
  }
  &-title {
    font-size: 16px;
    line-height: 150%;
    font-weight: 500;
    &:hover {
      color: #66AFFF;
    }
  }
  &-text {
    font-size: 14px;
    line-height: 150%;
    color: #3D404A;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient: vertical;
  }
}
.blog-products {
  padding: 25px 0 100px;
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 45px / 4));
    gap: 15px;
    margin-top: 25px;
  }
}
.blog-product__card {
  &-img {
    position: relative;
    display: block;
    padding-bottom: 100%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 20px;
    }
  }
  &-desc {
    margin-top: 15px;
  }
  &-title {
    font-size: 20px;
    line-height: 150%;
    font-weight: 600;
  }
  &-text {
    font-size: 16px;
    line-height: 150%;
    color: #3D404A;
  }
}

@media screen and (max-width: 992px) {
  .blog-main {
    padding-top: 180px;
    &__title {
      max-width: 500px;
      font-size: 32px;
    }
  }
  .blog-featured {
    &__cards {
      grid-template-columns: repeat(4, calc(25% - 90px / 4));
    }
  }
  .blog-product__card {
    &-title {
      font-size: 18px;
    }
  }
}

@media screen and (max-width: 768px) {
  .blog-main {
    padding-top: 160px;
  }
  .blog-featured {
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 60px / 3));
    }
  }
  .blog-products {
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 60px / 3));
      gap: 30px;
    }
  }
}

@media screen and (max-width: 576px) {
  .blog-main {
    padding-top: 120px;
    &__cards {
      gap: 40px;
    }
  }
  .blog-main__card {
    flex-direction: column;
    &-desc {
      width: 100%;
    }
    &-img {
      width: 100%;
      order: -1;
    }
  }
  .blog-featured {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
      gap: 20px;
    }
  }
  .blog-featured__card {
    &-img {
      img {
        border-radius: 10px;
      }
    }
  }
  .blog-products {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
      gap: 20px;
    }
  }
  .blog-product__card {
    &-desc {
      margin-top: 12px;
    }
    &-title {
      font-size: 16px;
    }
    &-text {
      font-size: 14px;
    }
  }
}