.about {
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 8px;
    p {
      font-size: 18px;
      line-height: 188%;
    }
  }
}
.about-main {
  padding: 200px 0 25px;
  &__title {
    font-size: 40px;
    line-height: 120%;
    font-weight: 600;
    letter-spacing: -0.8px;
  }
  &__subtitle {
    margin: 30px 0 8px;
  }
  &__wrap {
    display: flex;
    align-items: start;
    gap: 30px;
    margin-top: 30px;
  }
  &__img {
    width: 54%;
    img {
      width: 100%;
    }
  }
  &__desc {
    width: 46%;
  }
}
.about-feedback {
  padding: 25px 0;
  &__wrap {
    display: flex;
    align-items: start;
    gap: 30px;
    margin-bottom: 20px;
  }
  &__desc {
    width: 54%;
    .about__text {
      margin-top: 15px;
    }
  }
  &__img {
    width: 46%;
    img {
      width: 100%;
      border-radius: 30px;
    }
  }
}
.about-advantages {
  padding: 25px 0 100px;
  &__title {
    margin-bottom: 20px;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
    gap: 20px;
    margin: 20px 0 50px;
  }
  &__card {
    img {
      width: 100%;
      border-radius: 30px;
    }
  }
}

@media screen and (max-width: 992px) {
  .about {
    &__text {
      p {
        font-size: 16px;
      }
    }
  }
  .about-main {
    padding-top: 180px;
    &__title {
      max-width: 500px;
      font-size: 32px;
    }
    &__wrap {
      flex-direction: column;
    }
    &__img {
      width: 100%;
      max-width: 500px;
      margin: 0 auto;
    }
    &__desc {
      width: 100%;
    }
  }
  .about-feedback {
    &__wrap {
      flex-direction: column;
      margin-bottom: 30px;
    }
    &__desc {
      width: 100%;
    }
    &__img {
      width: 100%;
      max-width: 500px;
      margin: 0 auto;
    }
  }
  .about-advantages {
    &__cards {
      margin-bottom: 30px;
    }
  }
}

@media screen and (max-width: 768px) {
  .about-main {
    padding-top: 160px;
  }
}

@media screen and (max-width: 576px) {
  .about-main {
    padding-top: 120px;
  }
  .about-advantages {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 10px / 2));
      gap: 10px;
    }
  }
}